*******************************************************
******************************************************/
@media all and (max-width:1024px) {
    aside {
        width: 30%;
    }
    article {
        width: 70%;
    }
    .contacts_h span {
        font-size: 1em;
    }
    .scrolled .email_h {
        .translate3d(-160px, 40px);
    }
    .scrolled .tel_h {
        .translate3d(-325px, 80px);
    }
}
@media all and (max-width:767px) {
    section {
        aside {
            width:100%;
            text-align:center;
            h1 {
                margin-bottom:0;
            }
        }
        article {
            width:100%;
        }
    }
    header {
        position:absolute;
    }
    .scrolled {
        header {
            .translate3d();
        }
        .name {
            .translate3d();
        }
        .familyname {
            .translate3d();
        }
        .tel_h {
            .translate3d()
        }
        .email_h {
            .translate3d();
        }
    }
    .portfolio-item {
        width: 33.3333%;
    }
    .overlay {
        opacity: 0;
    }
}
@media all and (max-width:479px) {
    .bars li h3 {
        width: 45%;
    }
    .bars li>div {
        width: 45%;
    }
    h1.hidden {
        display:block;
    }
    .inner.first h1 {
        display:none;
    }
    .top_section {
        aside {
            width:100%;
            figure {
                float:left;
                width:25%;
            }
            h1 {
                width: 72.916666666666666666666666666667%;
                float: right;
                text-align: left;
            }
        }
        article {
            width:100%;
        }
    }
    .top_inner {
        margin: 0 1.3888888888888888888888888888889%;
        padding: 5px 0.69444444444444444444444444444444%;
    }
    .name_h span {
        font-size: 2.4em;
    }
    .contacts_h span {
        font-size: 1em;
        line-height: 150%;
    }
    .outer {
        padding-top: 90px;
    }
    header {
        height: 90px;
    }
    .name {
        top:7px;
    }
    .familyname {
        top:50px;
    }
    .tel_h {
        top:20px;
    }
    .email_h {
        top:33px;
    }
    .skype_h {
        top:50px;
        right: 10px;
    }
    .print_h {
        display: none;
    }
    .portfolio-item {
        width: 50%;
    }
}
@media all and (max-width:320px) {
    .date {
        margin:0;
        float:none;
        div {
            padding: 4px 0;
            width: 60px;
            text-align: center;
            font-size: 12px;
            float: right;
        }
    }
    .bars li h3 {
        width:53%;
    }
    .bars li > div {
        width: 35%;
    }
}
@import "print";